import React from "react";

import styled from "styled-components";

const Card = (props) => (
  <CardContainer>
    <p>
      {new Date(props.data.date).toLocaleDateString("en-US", {
        weekday: "long",
      })}
    </p>
    <img
      src={props.data.day.condition.icon}
      alt={props.data.day.condition.text}
    />
    <p>{Math.floor(props.data.day.maxtemp_c + 0.5)} Â°C</p>
    <p>{props.data.day.condition.text}</p>
  </CardContainer>
);

const CardContainer = styled.div`
  height: 80%;
  width: 30%;
  padding: 1%;
  :first-child,
  :nth-child(2) {
    border-right: 1px solid #eeeeee;
  }
`;

export default Card;
