import React from "react";

import styled from "styled-components";

import Header from "./components/header.js";
import Input from "./components/input.js";
import CardList from "./components/card-list.js";

class App extends React.Component {
  constructor(props) {
    super(props);

    this.inputRef = React.createRef();

    this.state = {
      weatherData: [],
      city: "Sofia",
      lastSuccessfullSearch: "Sofia",
    };
  }

  componentDidMount() {
    this.fetchWeather();
  }

  fetchWeather() {
    fetch(
      `https://api.weatherapi.com/v1/forecast.json?key=ecdee30c3b1e4c1cb0d83252201508&q=${this.state.city}&days=3`
    )
      .then((response) => response.json())
      .then((weatherData) => {
        this.setState({
          weatherData: weatherData.forecast.forecastday,
          lastSuccessfullSearch: weatherData.location.name,
        });
      })
      .catch((err) => console.log(err));
  }

  handleKeyDown = (event) => {
    if (event.key === "Enter") {
      this.handleSearch();
    }
  };

  handleSearch = () => {
    const city = this.inputRef.current.value;
    this.setState(
      {
        city,
      },
      () => {
        this.fetchWeather();
      }
    );
    this.inputRef.current.value = "";
  };

  render() {
    return (
      <AppContainer>
        <Header city={this.state.lastSuccessfullSearch} />
        <Input
          handleSearch={this.handleSearch}
          handleKeyDown={this.handleKeyDown}
          inputRef={this.inputRef}
        />
        <CardList weatherData={this.state.weatherData} />
      </AppContainer>
    );
  }
}

const AppContainer = styled.div`
  text-align: center;
  height: 100vh;
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
`;

export default App;
