import React from "react";

import styled from "styled-components";

const Header = (props) => (
  <HeaderContainer>
    <h1>
      3 Day <strong>{props.city}</strong> Weather Forecast
    </h1>
  </HeaderContainer>
);

const HeaderContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  h1 {
    font-size: 3rem;
  }

  strong {
    color: rgb(150, 31, 247);
  }
`;

export default Header;
